body {
  background-image: url("pacto-visual-cWOzOnSoh6Q-unsplash.442e4169.jpg");
  background-position: right 35% bottom 80%;
}

.loader {
  width: 48px;
  height: 48px;
  box-sizing: border-box;
  border-top: 3px dashed #ff3d00;
  border-bottom: 3px solid rgba(0, 0, 0, 0);
  border-left: 3px dotted #ff3d00;
  border-right: 3px solid #ff3d00;
  border-radius: 50%;
  animation: rotation 1s linear infinite;
  display: inline-block;
  position: relative;
}

.loader:after {
  content: "";
  box-sizing: border-box;
  border: 10px solid rgba(0, 0, 0, 0);
  border-right-color: #ff3d00;
  position: absolute;
  top: 31px;
  left: 20px;
  transform: rotate(-40deg);
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

.is-hidden {
  display: none;
}

.breed-select {
  width: 300px;
  margin-bottom: 30px;
}

